import {
  removeElem
} from "./helpers";
import {
  serverComm
} from "./serverComm";

let elemCoefficient = 1;
let hexGridArray = [];
let gameURL;
let gameOver = false;

function generateField(elemCoefficient) {
  const fieldSize = elemCoefficient;
  const gridArray = [];
  const value = 0;
  for (let i = -fieldSize; i < fieldSize + 1; i += 1) {
    for (let j = -fieldSize; j < fieldSize + 1; j += 1) {
      for (let k = -fieldSize; k < fieldSize + 1; k += 1) {
        if (i + j + k == 0) {
          gridArray.push({
            'x': i,
            'y': j,
            'z': k,
            'value': value,
          });
        }
      }
    }
  }
  console.log(gridArray);
  return gridArray;
}

function drawGrid(gridArray, elemCoefficient) {
  const field = document.querySelector(".game-field");
  const fieldWidth = field.offsetWidth;
  const fieldHeight = field.offsetHeight;

  const elemRadius = elemCoefficient * 2 - 1;
  const cellSize = fieldWidth / (elemCoefficient * 4 + 1);

  const cellWidth = (cellSize * 2).toFixed(2);
  const cellHeight = ((cellWidth * Math.sqrt(3)) / 2).toFixed(2);
  const divElem = document.createElement("div");
  divElem.className = "hex-grid";
  divElem.innerHTML = ``;

  let cellClass = "css-2";

  switch (elemCoefficient) {
    case 2:
      cellClass = "css-3";
      break;
    case 3:
      cellClass = "css-4";
  }

  let x, y, z, value;
  let posX, posY;
  let centerX = fieldWidth / 2;
  let centerY = fieldHeight / 2;

  for (let i = 0; i < gridArray.length; i++) {
    [x, y, z, value] = [
      gridArray[i].x,
      gridArray[i].y,
      gridArray[i].z,
      gridArray[i].value,
    ];
    posX = (x * 3 * cellWidth) / 4 + centerX - cellWidth / 2;
    posY = ((-y + z) * cellHeight) / 2 + centerY - cellHeight / 2;
    divElem.innerHTML += `
    <div class="${cellClass}" 
      data-value="${value}" 
      data-x="${x}" 
      data-y="${y}" 
      data-z="${z}" 
      style="left: ${posX}px; top: ${posY}px;">${value || ""}</div>
    `;
  }
  if (!!document.querySelector(".hex-grid")) {
    removeElem(".hex-grid");
  }
  field.append(divElem);
  document.querySelector(".game-status").innerHTML = `Game Status: ${gameOver ? 'Game Over' : 'Playing'}`;
}

function updateCells(gridArray, dataArray) {
  gridArray.forEach((element) => {
    dataArray.forEach((data) => {
      const result =
        element.x === data.x && element.y === data.y && element.z === data.z;
      if (!!result) {
        element.value = data.value;
      }
    });
  });
}

function initField(inputRadius, data, reqURL) {
  elemCoefficient = parseInt(inputRadius) - 1;
  hexGridArray = generateField(elemCoefficient);
  gameURL = reqURL;
  updateCells(hexGridArray, data);
  drawGrid(hexGridArray, elemCoefficient);
  return hexGridArray;
}

function getRowArray(axis, index, array) {
  const rowArray = [];
  array.forEach((element) => {
    if (element[axis] === index) {
      rowArray.push(element);
    }
  });
  return rowArray;
}

// **
// direction Boolean  - true for positive / false for negative
function getMovedRow(rowArray, direction) {
  const moveArr = JSON.parse(JSON.stringify(rowArray));
  const filteredInitArr = moveArr.filter(elem => elem.value > 0);
  const valuesArray = filteredInitArr.length && filteredInitArr.map(elem => elem.value) || [];
  const countZeroes = moveArr.length - valuesArray.length;
  const zeroesArray = new Array(countZeroes).fill(0);
  const newArray = direction ? [...zeroesArray, ...valuesArray] : [...valuesArray, ...zeroesArray];  
  for (const [index, value] of moveArr.entries()) {
    value.value = newArray[index];
  }
  return moveArr;
}

function getCalculatedRow(rowArray, direction) {
  const initArr = JSON.parse(JSON.stringify(rowArray));
  // console.log(`111: ${JSON.stringify(initArr)}`);
  if (direction) {
    for (let i = initArr.length - 1; i > 0; i--) {
      if (initArr[i].value === initArr[i - 1].value) {
        initArr[i].value += initArr[i - 1].value;
        initArr[i - 1].value = 0;
      }
    }
  } else {
    for (let i = 1; i < initArr.length; i++) {
      if (initArr[i - 1].value === initArr[i].value) {
        initArr[i - 1].value += initArr[i].value;
        initArr[i].value = 0;
      }
    }
  }
  // console.log(`101: ${JSON.stringify(hexGridArray)}`);
  // console.log(`102: ${JSON.stringify(rowArray)}`);
  // console.log(`103: ${JSON.stringify(initArr)}`);
  return initArr;
}

function getIndexArr(fieldRadius) {
  const indexArr = [];
  for (let i = -fieldRadius + 1; i < fieldRadius; i++) {
    indexArr.push(i);
  }
  return indexArr;
}

function generateNewArray(axis, fieldRadius, direction, incomingArray, resultArray, changeFunction) {
  const indexArr = getIndexArr(fieldRadius);

  for (let indexVal of indexArr) {
    let rowArray = getRowArray(axis, indexVal, incomingArray);
    let bufferRow = changeFunction(rowArray, direction);
    resultArray.push(...bufferRow);
  }
}

function getUpdate(axis, fieldRadius, direction) {
  console.log(`000: ${JSON.stringify(hexGridArray)}`);
  let movedHexGridArray = [];
  let calculatedHexGridArray = [];
  let updatedHexGridArray = [];
  console.log(`001: ${JSON.stringify(hexGridArray)}`);
  generateNewArray(axis, fieldRadius, direction, hexGridArray, movedHexGridArray, getMovedRow);
  console.log(`002: ${JSON.stringify(hexGridArray)}`);
  generateNewArray(axis, fieldRadius, direction, movedHexGridArray, calculatedHexGridArray, getCalculatedRow);
  console.log(`003: ${JSON.stringify(hexGridArray)}`);
  console.log(`111: ${JSON.stringify(calculatedHexGridArray)}`)
  if (isEqualArr(calculatedHexGridArray, hexGridArray)) {
    console.log('!!!!');
    return;
  }

  generateNewArray(axis, fieldRadius, direction, calculatedHexGridArray, updatedHexGridArray, getCalculatedRow);

  // console.log(`222: ${JSON.stringify(hexGridArray)}`);
  // console.log(`333: ${JSON.stringify(updatedHexGridArray)}`);
  // check changes in cells array

  // addNewCellData(updatedHexGridArray);
  // hexGridArray = updatedHexGridArray;

  drawGrid(updatedHexGridArray, elemCoefficient);
  gameOver = gameOverCheck(updatedHexGridArray, fieldRadius);
}

function addNewCellData(incomingArray) {
  serverComm(gameURL, incomingArray.filter(elem => elem.value > 0)).then(result => {
    incomingArray.map(elem => {
      result.forEach(data => {
        const result =
          elem.x === data.x && elem.y === data.y && elem.z === data.z;
        if (!!result) {
          elem.value = data.value;
        }
      });
    });
  });
}

function gameOverCheck(incomingArray, fieldRadius) {
  let count = 0;
  const indexArr = getIndexArr(fieldRadius)

  for (let axis of ['x', 'y', 'z'])
    for (let indexVal of indexArr) {
      let rowArray = getRowArray(axis, indexVal, incomingArray);
      for (let i = 1; i < rowArray.length; i++) {
        if (rowArray[i].value === rowArray[i - 1].value || rowArray[i].value === 0) {
          count++
        };
      }
    }
  console.log(count);
  return !count;
}

function isEqualArr(arr1, arr2) {
  for (let i = 0; i < arr1.length; ++i) {
    // console.log(arr1[i].toString());
    // console.log(arr2[i].toString())
    if (arr1[i].toString() != arr2[i].toString()) return false;
  }

  return true;
}

export {
  initField,
  updateCells,
  drawGrid,
  getUpdate
};
